@import "../../shared/index";

.promo {
  display: flex;
  flex-direction: row;
  justify-content: center;
  min-height: 408px;

  &__description {
    width: 237px;
    font-size: 36px;
    font-weight: bold;
    line-height: 1.11;
    text-align: center;
    color: @white;
    margin-top: 22px;
  }

  @media (min-width: @tablet-width) {
    min-height: 321px;

    &__description {
      width: 395px;
      height: 196px;
      font-size: 60px;
      line-height: 1.17;
      margin-top: 68px;
    }
  }

  @media (min-width: @desktop-width) {
    width: 1064px;
    justify-content: flex-start;

    &__description {
      margin-top: 106px;
      margin-left: 127px;
      text-align: left;
    }
  }
}
