@font-face {
  font-style: normal;
  font-display: swap;
  font-weight: normal;
  font-family: "BloggerSans";
  src: url("../fonts/bloggersans.woff2"), url("../fonts/bloggersans.woff");
}
@font-face {
  font-style: normal;
  font-display: swap;
  font-weight: bold;
  font-family: "BloggerSans";
  src: url("../fonts/bloggersans-bold.woff2"), url("../fonts/bloggersans-bold.woff");
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "BloggerSans", "Arial", sans-serif;
  background-color: #ffffff;
}
img {
  max-width: 100%;
  height: auto;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
a {
  text-decoration: none;
}
.button {
  cursor: pointer;
  border: none;
  font-family: "BloggerSans", "Arial", sans-serif;
}
.button-arrow {
  position: relative;
  background-color: #ffd74b;
  border-radius: 10px;
  font-size: 18px;
  font-weight: bold;
  line-height: 1;
  letter-spacing: normal;
  text-align: left;
  color: #1d2e5b;
  padding-left: 24px;
}
.button-arrow::after {
  position: absolute;
  content: "";
  width: 10px;
  height: 14px;
  background: url("../img/custom/arrow-right-dark-slate-blue.svg") center no-repeat;
  background-size: 10px 14px;
  top: 25px;
  right: 23px;
}
.button-arrow:hover {
  outline: none;
  background-color: #ffbd30;
}
.button-arrow:active {
  outline: none;
  opacity: 0.3;
}
@media (min-width: 768px) {
  .button-arrow {
    padding-left: 34px;
    font-size: 24px;
    font-weight: bold;
    line-height: 1;
    border-radius: 20px;
  }
  .button-arrow::after {
    top: 35px;
    right: 33px;
  }
}
.body-container {
  width: 320px;
}
@media (min-width: 768px) {
  .body-container {
    width: 768px;
  }
}
@media (min-width: 1440px) {
  .body-container {
    width: 1440px;
  }
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.social__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
}
.social__item {
  border-radius: 50%;
}
.social__item--colored {
  background-color: #ffd74b;
}
.social__icon {
  width: 45px;
  height: 45px;
  border-radius: 50%;
}
.social__item--colored .social__icon:hover {
  background-color: #ffbd30;
}
.social__item:not(.social__item--colored) .social__icon:hover {
  opacity: 0.6;
}
.social__icon:active {
  opacity: 0.3;
}
.social__icon--fb {
  background: url("../img/facebook.svg") no-repeat center;
  background-size: 11px 21px;
}
.social__icon--vk {
  background: url("../img/vk.svg") no-repeat center;
  background-size: 23px 12px;
}
.social__icon--ig {
  background: url("../img/instagram.svg") no-repeat center;
  background-size: 18px 18px;
}
@media (min-width: 768px) {
  .social {
    width: 165px;
  }
  .social__list {
    width: 100%;
    justify-content: space-between;
  }
  .social__icon {
    width: 60px;
    height: 60px;
  }
  .social__icon--fb {
    background-size: 14px 28px;
  }
  .social__icon--vk {
    background-size: 31px 16px;
  }
  .social__icon--ig {
    background-size: 24px 24px;
  }
}
.main-wrapper {
  padding-left: 25px;
  padding-right: 25px;
}
.footer {
  border-top: 1px solid transparent;
  position: relative;
  border-radius: 30px 30px 0 0;
  width: 100%;
  margin-top: -25px;
  background-color: #ffffff;
}
.footer::before {
  position: absolute;
  content: "";
