    height: 60px;
    background-color: #ffffff;
    top: 0;
  }
  .footer__wrapper {
    min-height: 154px;
    flex-direction: row;
    align-items: center;
    padding: 43px 38px 41px 42px;
  }
  .footer__socials {
    margin-top: 5px;
    margin-left: -28px;
