.navigation {
  z-index: 3;
}
.navigation__toggle {
  cursor: pointer;
  background-repeat: no-repeat;
  background-color: transparent;
  border: 0;
}
.navigation--opened .navigation__toggle {
  background: url("../img/filter-close.svg");
  width: 19px;
  height: 19px;
  z-index: 3;
  position: relative;
}
.navigation--closed .navigation__toggle {
  background-image: url("../img/menu-white.svg");
  width: 25px;
  height: 11px;
  z-index: -1;
}
.navigation--closed .navigation__container {
  display: none;
}
.navigation--opened .navigation__container {
  display: flex;
  width: 100%;
  min-height: 568px;
  position: absolute;
  top: 0;
}
.navigation--opened .nav-fixed__link {
  color: #192144;
}
.navigation--opened .nav-fixed__link:hover {
  color: #ffd74b;
}
.navigation--opened .nav-fixed__link:active {
  color: rgba(255, 215, 75, 0.3);
}
@media (min-width: 768px) {
  .navigation__toggle {
    margin-left: 24px;
  }
  .navigation--opened .navigation__container {
    min-height: 910px;
  }
}
@media (min-width: 1440px) {
  .navigation {
    display: block;
    position: static;
  }
  .navigation__toggle {
    display: none;
  }
  .navigation--closed .navigation__container,
  .navigation--opened .navigation__container {
    display: block;
  }
  .navigation--opened .navigation__container {
    display: block;
    width: 388px;
    min-height: auto;
    position: relative;
    top: auto;
  }
  .navigation--opened .nav-fixed__link {
    color: #ffffff;
  }
  .navigation__container {
    min-height: 16px;
  }
}
