@import "../../shared/index";

.index-header {
  position: relative;
  min-height: 763px;

  background-image: url("@{imgPath}/traveller-illustration-mobile@1x.png"), url("@{imgPath}/globe-back-mobile@1x.jpg");
  background-position: 79px 223px, 0 0;
  background-size: 192px 255px, 320px 569px;
  background-repeat: no-repeat;
  background-color: @bg-default;

  &__menu-wrapper {
    min-height: 70px;
    padding-top: 27px;
    padding-left: 25px;
    padding-right: 25px;
  }

  &__advantages {
    position: absolute;
    width: 100%;
    bottom: -240px;
  }

  @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
    background-image: url("@{imgPath}/traveller-illustration-mobile@2x.png"), url("@{imgPath}/globe-back-mobile@2x.jpg");
  }

  @media (min-width: @tablet-width) {
    min-height: 994px;

    background-image: url("@{imgPath}/traveller-illustration-tablet@1x.png"), url("@{imgPath}/globe-back-tablet@1x.jpg");
    background-position: 284px 403px, -355px 381px;
    background-size: 372px 451px, 1479px 612px;

    &__menu-wrapper {
      min-height: 90px;
      padding-top: 40px;
      padding-left: 40px;
      padding-right: 40px;
    }

    &__advantages {
      bottom: -140px;
    }

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("@{imgPath}/traveller-illustration-tablet@2x.png"), url("@{imgPath}/globe-back-tablet@2x.jpg");
    }
  }

  @media (min-width: @desktop-width) {
    min-height: 750px;

    background-image: url("@{imgPath}/traveller-illustration-desktop@1x.png"), url("@{imgPath}/globe-back-desktop@1x.jpg");
    background-position: 604px 110px, -180px 0;
    background-size: 431px 640px, 1800px 750px;

    &__menu-wrapper {
      padding-top: 48px;
      padding-left: 60px;
      padding-right: 60px;
    }

    &__advantages {
      bottom: 123px;
    }

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("@{imgPath}/traveller-illustration-desktop@2x.png"), url("@{imgPath}/globe-back-desktop@2x.jpg");
    }
  }
}
