@import "../../shared/index";

.step3 {
  margin-bottom: 44px;

  &__controls {
    display: flex;
    flex-direction: column;
    margin-top: 19px;
  }

  @media (min-width: @tablet-width) {
    margin-top: 54px;

    &__controls {
      flex-direction: row;
      justify-content: space-between;
      margin-top: 41px;
    }
  }

  @media (min-width: @desktop-width) {
    margin-top: 47px;

    &__header-hint {
      width: 500px;
    }

    &__controls {
      margin-top: 48px;
    }
  }
}
