@import "../../shared/index";

.directions {
  min-height: 990px;
  background-color: @pale-grey;
  .z-background-bottom(30px, @dark-blue-grey);

  &__wrapper {
    display: flex;
    flex-direction: column;
    padding-top: 71px;
    min-height: inherit;
  }

  @media (min-width: @tablet-width) {
    min-height: 1094px;
    .z-background-bottom(60px, @dark-blue-grey);

    &__wrapper {
      padding-left: 76px;
      background-image: url("@{imgPath}/flags-back@1x.png");
      background-size: 289px 575px;
      background-repeat: no-repeat;
      background-position: 439px 80px;
    }

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      &__wrapper {
        background-image: url("@{imgPath}/flags-back@2x.png");
      }
    }
  }

  @media (min-width: @desktop-width) {
    min-height: 751px;

    &__wrapper {
      flex-direction: row;
      padding-top: 161px;
      padding-left: 98px;
      padding-right: 98px;
      background-position: 1091px 88px;
    }
  }
}
